
* {
	margin: 0;
	padding: 0;
}
@media screen and (min-width: 68vh){
#play-bar, #search-bar, body, #app{
		width: 68vh;
		margin: 0 auto
}
}
html {
	overflow-x: hidden;
	background: #EEEEEE;
}
body {

	overflow-x: hidden;
}
#app {
	font-family: -apple-system, BlinkMacSystemFont, "PingFang SC", "Helvetica Neue", STHeiti, "Microsoft Yahei", Tahoma, Simsun, sans-serif;
	width: 100%;
	height: 100%;
}
#app a {
	color: #42b983;
	text-decoration: none;
}



